export default {
	data() {
		return {
			emailRules: [
				v => !!v || ' .',
				v =>
					/[\S]+@[\S\D]+\.[\S\D]+/gi.test(v) ||
					'   .'
			],
			passwordRules: [
				v => !!v || ' .',
				v => /[0-9]/g.test(v) || '  .',
				v => /[a-z]/gi.test(v) || '   .',
				v => /[~!@#$%^&*/?]/g.test(v) || '  .',
				v =>
					!(v.length < 8 || v.length > 20) ||
					'   .',
				v =>
					/[^-]/gi.test(v) ||
					'  .  .'
			]
		};
	}
};
